<template>
<div id="play-layout" v-if="!!userDetails">
  <div id="main">
      <!-- Sidebar -->
      <SideNav class="side-nav" :class="{show_nav: toggleMenu}" :user="userDetails"/>

      <div id="content">
        <!-- Header -->
        <play-header @toggle="toggleMenu = !toggleMenu"/>

        <div class="page">
          <router-view></router-view>     
        </div>
          
      </div>

  </div>
  <div class="foot">
    <Footer/>
  </div>
   
</div>
</template>

<script>
import { mapState, mapMutations, mapActions } from 'vuex';
import PlayHeader from "@/components/AppHeader";
import Footer from "@/components/AppFooter";
const SideNav = () => import("@/components/sidenavs/ResultsideNav")

export default {
  name: "components",
  components: {
    PlayHeader,
    Footer,
    SideNav
  },
  data(){
    return { 
      toggleMenu: false
    }
  },
  async mounted(){
      await this.getuserDetails()
    
   
  },
  methods:{
    ...mapActions(['getuserDetails']),
  },
  computed:{
    ...mapState(['userDetails'])
  }
};
</script>
<style scoped>
#play-layout{
 background: #E5E5E5;
}
#play-layout #main{
  display:flex;
}
#play-layout #main #content{
  width: 100%;
}
#play-layout #main .header{
  background: #fff;
}
#play-layout #main .page-title{
  background: transparent;
}
#play-layout #main .page{ 
  width: 100%;
  }
#play-layout .foot{
  margin-top: 2rem;
  padding-top: 3rem !important;
}
@media screen and (max-width:760px) {
.side-nav{
  display: none !important;
}
.show_nav{
  display: block !important;
}
}


</style>
